.AboutMe {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .box {
    padding: 20px;
    display: flex;
    justify-content: center;
  }
  
  .image-box img {
    padding: 50px;
    width: 100%;
    height: auto;
    max-width: 100%;
  }
  
  .text-box {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }
  
  .text-box p {
    font-size: 16px;
    color: #333;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  
  .AboutMe h2 {
    text-align: left;
    font-family: "ivypresto-display", serif;
    font-weight: 300;
    font-style: italic;
    font-size: 30px;
    margin-top: 0px;
  }
  
  .AboutMe h1 {
    margin-bottom: 10px;
  }
  
  @media screen and (max-width: 950px) {
    .container {
      display: flex;
      flex-direction: column;
      align-items: center; 
    }
  
    .box {
      width: 100%; 
      padding: 10px;
    }
  
    .text-box {
      order: -1; 
      width: 100%; 
    }
  
    .image-box {
      width: 100%;
    }
  
    .image-box img {
      padding: 20px;
    }

    .AboutMe h1{
        margin-top: 0px
    }
  }
  