.pfp-div {
  text-align: center;
}

hr {
  margin-top: 50px;
  margin-bottom: 10px;
  width: 60%;
  border: none;
  border-top: 1px solid black;
}

#pfp {
  width: 200px;
  border-radius: 100%;
}

h1 {
  font-family: "ivypresto-display", serif;
  font-weight: 500;
  margin-top: 30px;
  font-style: normal;
  letter-spacing: 1px;
  font-size: 60px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
  gap: 50px;
  margin: 50px;
  margin-top: 10px;
  font-family: "ivypresto-display", serif;
  font-weight: 300;
  font-style: normal;
  font-size: 30px;
  align-items: start;
}

.Portfolio h2 {
  font-family: "ivypresto-display", serif;
  font-weight: 300;
  font-style: italic;
  font-size: 30px;
  margin-left: 30px;
}

.gallery-item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: stretch;
  overflow: hidden;
}

.gallery-item a {
  display: block;
  position: relative;
  height: 100%;
}

.gallery img {
  width: 100%;
  height: auto;
  object-fit: contain;
  transition: opacity 0.3s ease;
}

.gallery-item figcaption {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  color: #000000;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "ivypresto-display", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 25px;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  padding: 10px;
}

.gallery-item:hover img {
  opacity: 0.7;
}

.gallery-item:hover figcaption {
  opacity: 1;
}

@media screen and (max-width: 600px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    margin: 20px;
  }
}
@media screen and (max-width: 400px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin: 20px;
    margin-top: 5px;
  }
}
